
.container-form {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-top: 96px;
}

.container-form > .traco{
    min-width: 330px;
    margin-bottom: 65px;
}

.container-form h1 {
    margin-bottom: 10px;
    text-align: center;
    color: var(--marrom);
}

form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.selectContainer, .containerObs, .containerInput{
    background-color: rgba(150, 45, 110, 0.384);
    padding: 8px;
    margin-bottom: 8px;
    display: flex;
    border-radius: 8px;
    box-shadow: 1px 2px 6px rgba(0, 0, 0, 0.5);
}

.containerInput, .selectContainer {
    max-width: 100%;
    min-width: 100%;
}

.containerInput input {
    min-width: 100%;
    min-height: 37px;
}

.containerInput input::placeholder{
    color: #807c7c;
}


.selectContainer select{
   min-width: 100%;
}

.containerObs textarea{
    background: transparent;
    border: 0;
    outline: none;
    color: #807c7c;
}

selct{
    color: #807c7c;
}

input , select{
    min-height: 30px;
    max-width: 220px;
    outline: none;
    border: 0;
    background: transparent;
    
}

#observacao {
    margin-bottom: 5px;
}

.form-group {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 0.8em;
}

.form-group > p {
    margin-top: 2px;
    margin-bottom: 0;
}

.opt_ct_fidelidade {
    display: flex;
    flex-direction: row;
    max-height: 21px;
    margin-bottom: 3px;
}

.form-check{
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    flex-direction: row;
    margin-left: 5px;
}

.bt-pedido{
    min-width: 100%;
    border: 0;
    background-color: var(--marrom);
    border-radius: 10px;
    min-height: 50px;
    margin-top: 15px;
    margin-bottom: 80px;
    color: white;
    box-shadow: 4px 7px 13px 0px rgb(128, 125, 125);
}

@media screen and (max-width:500px) {
        .containerInput, .selectContainer {
            max-width: 90%;
            min-width: 89%;
        }
    }