.gallery {
    width: 100%;
    max-width: 1338px;
    margin: 0 auto;
    min-height: 50vh;
    display: flex;
    align-items: center;
    justify-content: center;
}


.gallery-center > h1{
    margin-top: 30px;
    text-align: center;
    color: var(--marrom);
    border-style: solid;
    border: 0px;
    border-bottom: 10px;
    border-color: var(--rosalogo);
}

.inner {
    display: flex;

}

.item-img {
    min-height: 200px;
    min-width: 400px;
    padding: 14px;
}

.item-img img {
    width: 100%;
    height: 90%;
    border-radius: 15px;
    pointer-events: none;
}

.carousel {
    cursor: grab;
    overflow: hidden;
}

@media screen and (max-width:870px) {
    .item-img {
        min-height: 451px;
        min-width: 304px !important;

    }

    .gallery-center .traco{
        margin-bottom: 18px;
    }

    .gallery-center h1{
        margin-top: 50px;
    }
}